<template>
  <div class="result">
    <div class="result__content">
      <router-link class="result__refresh" to="/#testScroll">
        <img src="../assets/images/refresh.png" alt="refresh"
      /></router-link>
      <p class="result__text text-bold result__again">   </p>
      <h3 class="result__title text-bold">
        {{ answer }}
        {{ answer === 1 ? " " : " " }}
      </h3>
      <p class="result__text result__text-paragraf">
        {{ result.paragrafOne }}
      </p>
      <p class="result__text result__text-paragraf">
        {{ result.paragrafTwo }}
      </p>
      <div class="result__btns">
        <a class="result__btn-share btn-result" href="#"></a>
        <Popup />
      </div>
    </div>
  </div>
</template>

<script>
import Popup from "@/components/Popup.vue";
export default {
  name: "Result",
  data() {
    return {
      answer: 0,
      result: {
        paragrafOne:
          "      !  !       ,    ,   ,     . ,     Huawei Freebuds 4i,      !     10    ,     ,     !",
        paragrafTwo:
          " ,           .  -   ,    Huawei Freebuds 4i         .          .",
      },
      resultTwo: {
        paragrafOne:
          "    ,      !          , ,   !      Huawei Freebuds 4i    ,            ! ",
        paragrafTwo:
          "         ,     -  . Huawei Freebuds 4i    4-    10 .     !",
      },
      resultThree: {
        paragrafOne:
          "    ,      !   ,   ,     ,   .  !         Huawei Freebuds 4i.  ,       ,         !    ,     ,      ! ",
        paragrafTwo:
          " , Huawei Freebuds 4i   , ,           -    . ",
      },
      resultFour: {
        paragrafOne:
          "    !    ,   !   ,      !   -   Huawei Freebuds 4i,      10 ,           . ,     ,   !",
        paragrafTwo:
          "Huawei Freebuds 4i     iOS,    Android.        , ,     .",
      },
    };
  },
  components: {
    Popup,
  },
  mounted() {
    let resultNumber = this.$router.currentRoute.value.query.count;
    if (resultNumber >= 20 && resultNumber <= 30) {
      this.result = this.resultTwo;
    } else if (resultNumber >= 40 && resultNumber <= 50) {
      this.result = this.resultThree;
    } else if (resultNumber >= 60 && resultNumber <= 70) {
      this.result = this.resultFour;
    }

    if (this.$router.currentRoute.value.query.count) {
      this.answer = this.$router.currentRoute.value.query.count / 10;
      let query = Object.assign({}, this.$route.query);
      delete query.count;
      this.$router.replace({ query });
    } else {
      this.$router.push("/");
    }
  },
};
</script>

<style lang="scss" scoped>
.result {
  height: 100vh;
  background: url("../assets/images/result_bg.png") center no-repeat;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  text-align: center;
  &__content {
    max-width: 670px;
    width: 100%;
  }
  &__refresh {
    display: block;
    width: 80px;
    margin: 0 auto 15px;
  }
  &__text {
    font-size: 18px;
    line-height: 21px;
    &-paragraf {
      margin-bottom: 25px;
      padding: 0 10px;
      &:nth-child(5) {
        margin-bottom: 35px;
      }
    }
  }
  &__again {
    margin-bottom: 35px;
  }
  &__title {
    font-size: 24px;
    line-height: 28px;
    margin-bottom: 10px;
  }
  &__btns {
    display: flex;
    justify-content: space-between;
  }
  &__btn {
    &-share {
      background: #1877f2;
      position: relative;
      &::before {
        content: "";
        position: absolute;
        width: 10px;
        height: 20px;
        background: url("../assets/images/share.svg") center no-repeat;
        top: 50%;
        transform: translateY(-50%);
        right: 28%;
      }
    }
  }
}
</style>
